/* height: -webkit-fill-available; */


/* ESQUEMAS DE CORES */

:root .ghapscheme {
    --botao: #2c2c60;
    --botaohover: #534c7a;
    --cor1: #ffffff;
    --cor2: #f2f2f2;
    --cor3: #e57e34;
    --cor4: #ec9e62;
    --fonte: #61636e;
}

:root .purplescheme {
    --botao: #8f9bbc;
    --botaohover: #b5bed8;
    --cor1: #ffffff;
    --cor2: #f2f2f2;
    --cor3: grey;
    --cor4: darkgrey;
    --fonte: #61636e;
}

:root .bluescheme {
    --botao: #7fb3d5;
    --botaohover: #a9cce3;
    --cor1: #ffffff;
    --cor2: #ebf5fb;
    --cor3: #2874a6;
    --cor4: #2e86c1;
    --fonte: #61636e;
}

.corprincipal {
    background-color: var(--botao);
}

.corescura {
    background-color: var(--cor3);
}

.corfundo {
    background-color: var(--cor2);
}


/* OCULTANDO CABEÇALHO E RODAPÉ PARA IMPRESSÃO */

@media print {
    #Header,
    #Footer {
        display: none !important;
    }
}


/* CONTAINERS */

body {
    -webkit-overflow-scrolling: touch;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    height: 100%;
    /*height: -webkit-fill-available;*/
    width: 100%;
    overflow: hidden;
    touch-action: manipulation;
}

.disable-dbl-tap-zoom {
    touch-action: manipulation;
}

.main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100vw;
    border-radius: 0px;
    margin: 0px;
    padding: 0px;
    font-size: 14px;
    cursor: default;
    background-color: var(--cor2);
    overflow-y: hidden;
    overflow-x: hidden;
}

.secondary {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    margin: 5px;
    cursor: default;
}

.menucover {
    background-color: rgba(0, 0, 0, 0.6);
    border-radius: 0;
    position: fixed;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100%;
    width: 100%;
    margin: 0;
    z-index: 1;
}

.menuposition {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.6);
    align-items: center;
    border-radius: 0px;
    padding: 0px;
    cursor: default;
    z-index: 1;
}


/* JANELAS */

.menucontainer {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: var(--cor2);
    align-items: center;
    border-radius: 5px;
    padding: 0px;
    cursor: default;
    z-index: 2;
    box-shadow: 0px 2px 20px 5px rgba(0, 0, 0, 0.5);
}

.cabecalho {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    border-top-left-radius: 0px;
    border-top-right-radius: 0px;
    background-color: var(--cor3);
    padding: 10px;
    align-items: center;
    font-size: 16px;
    color: #ffffff;
    font-weight: bold;
}

.corpo {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    background-color: var(--cor2);
    align-items: center;
    padding: 20px;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    cursor: default;
    z-index: 2;
}

.detalhes {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    flex-direction: column;
    align-items: flex-start;
    color: #ffffff;
    background-color: var(--cor4);
    border-radius: 5px;
    position: absolute;
    top: 10px;
    left: 10px;
    width: 40vw;
    z-index: 3;
    box-shadow: 0px 2px 10px 5px rgba(0, 0, 0, 0.5);
}


/* SCROLL */

.scroll {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: "center";
    overflow-y: scroll;
    overflow-x: hidden;
    background-color: transparent;
    border-color: transparent;
    border-style: solid;
    border-width: 5px;
    border-radius: 5px;
    cursor: default;
    width: 100%;
    margin: 0px;
    padding: 5px;
    padding-right: 18px;
    box-shadow: none;
    transition: 500ms, 500ms ease-in-out;
}

.scroll:hover {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: "center";
    overflow-y: scroll;
    overflow-x: hidden;
    background-color: #ffffff;
    border-color: #ffffff;
    border-style: solid;
    border-width: 5px;
    border-radius: 5px;
    cursor: default;
    width: 100%;
    margin: 0px;
    padding: 5px;
    padding-right: 18px;
    box-shadow: none;
    transition: 500ms, 500ms ease-in-out;
}

.scroll::-webkit-scrollbar {
    width: 10px;
    transition: 500ms, 500ms ease-in-out;
}

.scroll::-webkit-scrollbar-track {
    background-color: transparent;
    border-radius: 5px;
    transition: 500ms, 500ms ease-in-out;
}

.scroll:hover::-webkit-scrollbar-track {
    background-color: #dce1ec;
    border-radius: 5px;
    transition: 500ms, 500ms ease-in-out;
}

.scroll::-webkit-scrollbar-button {
    display: none;
    transition: 500ms, 500ms ease-in-out;
}

.scroll::-webkit-scrollbar-corner {
    display: none;
    transition: 500ms, 500ms ease-in-out;
}

.scroll::-webkit-scrollbar-thumb {
    background-color: transparent;
    border-radius: 5px;
    transition: 500ms, 500ms ease-in-out;
}

.scroll:hover::-webkit-scrollbar-thumb {
    background-color: var(--botao);
    border-radius: 5px;
    transition: 500ms, 500ms ease-in-out;
}

.scroll::-webkit-scrollbar-thumb:hover {
    background-color: var(--botaohover);
    border-radius: 5px;
    transition: 500ms, 500ms ease-in-out;
}

.scrollmenu {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    overflow-y: scroll;
    overflow-x: hidden;
    background-color: var(--cor3);
    border-color: var(--cor3);
    border-style: solid;
    border-width: 5px;
