.row {
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 100%;
    background-color: #ffffff;
    border: 0px;
    border-color: transparent;
    border-radius: 5px;
    outline: 0px;
    padding: 5px;
    margin: 5px;
    opacity: 0.8;
    cursor: default;
    box-shadow: 0px 2px 10px 2px rgba(0, 0, 0, 0.1);
    transition: 200ms;
}

.row:hover {
    display: flex;
    opacity: 1;
}

.rowitem {
    display: flex;
    flex-direction: row;
    justify-content: center;
    background-color: transparent;
    border: 0px;
    border-color: transparent;
    outline: 0px;
    padding: 5px;
    margin: 0px;
    cursor: default;
    color: #61636e;
    font-weight: bold;
    align-self: center;
    text-align: center;
    vertical-align: center;
}

.row:hover .rowbutton {
    display: flex;
    animation: rowbutton-in 200ms forwards;
}

@keyframes rowbutton-in {
    from {
        height: 0px;
        min-height: 0px;
        width: 0px;
        min-width: 0px;
        opacity: 0;
    }
    to {
        height: 50px;
        min-height: 50px;
        width: 50px;
        min-width: 50px;
        opacity: 1;
    }
}

.row:not(:hover) .rowbutton {
    display: flex;
    animation: rowbutton-out 0ms forwards;
}

@keyframes rowbutton-out {
    from {
        height: 50px;
        min-height: 50px;
        width: 50px;
        min-width: 50px;
        opacity: 1;
    }
    to {
        height: 0px;
        min-height: 0px;
        width: 0px;
        min-width: 0px;
        opacity: 0;
    }
}

