/* ANIMAÇÕES */

.fade-in {
    animation-name: fadein;
    animation-duration: 500ms;
}

@keyframes fadein {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes fadeout {
    from {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}

@keyframes cardload {
    0% {
        opacity: 0;
        height: 50%;
        width: 30vw;
    }
    100% {
        opacity: 0.8;
        height: "";
        width: "";
    }
}

.pulsarlogo {
    animation-name: pulsarlogo;
    animation-duration: 1s;
    animation-delay: 0ms;
    animation-iteration-count: infinite;
}

@keyframes pulsarlogo {
    0% {
        opacity: 0.3;
        height: 8vw;
    }
    50% {
        opacity: 1;
        height: 16vw;
    }
    100% {
        opacity: 0.3;
        height: 8vw;
    }
}

/* animações para calendário da escala de trabalho */
.escala {
    height: 50px;
    width: 12vw;
    transition: 500ms 1500ms ease-in-out;
}

.escala:hover {
    height: 30vw;
    width: 30vw;
    transition: 500ms 1500ms ease-in-out;
}

/* animações para a tela de login */
.manequim-in {
    display: flex;
    opacity: 1;
    z-index: 1;
    transition: 500ms 2000ms ease-in-out;
}

.manequim-out {
    display: none;
    opacity: 0;
    z-index: -1;
    transition: 500ms 200ms ease-in-out;
}


/* animação para o gráfico da lista de pacientes */

.datachart:hover {
    animation: datacharton 200ms ease-in-out forwards;
}

@keyframes datacharton {
    0% {
        transform: translateY(0px);
    }
    100% {
        transform: translateY(-20px);
    }
}

.datachart:not(:hover) {
    animation: datachartoff 200ms ease-in-out forwards;
}

@keyframes datachartoff {
    0% {
        transform: translateY(-20px);
    }
    100% {
        transform: translateY(0px);
    }
}

.expandcard {
    width: 90vw;
    opacity: 1;
    border-color: transparent;
    transition: 500ms ease-in-out;
}

.retractcard {
    width: 0vw;
    opacity: 0;
    border-color: transparent;
    transition: 500ms ease-in-out;
}

.legenda:hover .legendinha {
    opacity: 1;
    height: 50px;
    transition: 250ms ease-in-out;
}

.legenda:not(:hover) .legendinha {
    opacity: 0;
    height: 0px;
    transition: 250ms ease-in-out;
}


/* animação para o menu flutuante da tela prontuário (sidebar) */

.pacientes-menu-in {
    left: 0px;
    transition: 0.5s;
}

.pacientes-menu-stay {
    left: 0px;
}

.pacientes-menu-out {
    left: -30vw;
    transition: 0.5s;
}


/* toast */

.toast {
    animation: fadeinout 3s ease-in-out forwards;
}

@keyframes fadeinout {
    0% {
        opacity: 0;
    }
    10% {
        opacity: 1;
    }
    90% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}


/* animação para seleção de invasões. */

.animatedclick {
    animation-name: animatedclick;
    animation-duration: 2s;
    animation-iteration-count: infinite;
}

@keyframes animatedclick {
    0% {
        opacity: 0.5;
    }
    50% {
        background-color: tomato;
        opacity: 1;
    }
    100% {
        opacity: 0.5;
    }
}


/* animação para confirmação de exclusão de um registro (DeleteToast). */

.deletetoast {
    animation-name: deletetoast;
    animation-duration: 3s;
}

@keyframes deletetoast {
    from {
        width: 0px;
    }
    to {
        width: 100%;
    }
}


