.cardscroll::-webkit-scrollbar {
    width: 10px;
}

.cardscroll::-webkit-scrollbar-track {
    background-color: var(--cor2);
    border-radius: 5px;
}

.cardscroll::-webkit-scrollbar-button {
    display: none;
}

.cardscroll::-webkit-scrollbar-corner {
    display: none;
}

.cardscroll::-webkit-scrollbar-thumb {
    background-color: var(--botao);
    border-radius: 5px;
}

.cardscroll::-webkit-scrollbar-thumb:hover {
    background-color: var(--botaohover);
    border-radius: 5px;
}


/* PLACEHOLDER */

::placeholder {
    color: #a6acaf;
    opacity: 1;
    text-shadow: none;
}

.darkinput::placeholder {
    color: var(--botao);
    opacity: 1;
    text-shadow: none;
}


/* TITLES */

.row.tip:hover {
    cursor: help;
    position: relative;
}


/* RADIO */

.radio-toolbar input[type="radio"] {
    opacity: 0;
    position: fixed;
    width: 0;
    cursor: default;
}

.radio-toolbar label {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: #ffffff;
    font-weight: bold;
    min-width: 50px;
    min-height: 50px;
    height: 50px;
    width: 100px;
    background-color: var(--botao);
    border: 0px;
    border-color: transparent;
    border-radius: 5px;
    outline: 0px;
    box-shadow: transparent;
    padding-left: 10px;
    padding-right: 10px;
    margin-top: 0px;
    margin-bottom: 5px;
    margin-left: 2.5px;
    margin-right: 2.5px;
    cursor: default;
}

.radio-toolbar input[type="radio"]:focus+label {
    background-color: var(--botaohover);
    cursor: default;
}

.radio-toolbar input[type="radio"]:checked+label {
    background-color: #ec7063;
    cursor: default;
}

.boneco {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.logo {
    display: flex;
}

.username {
    display: flex;
}


